body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#page-container {
  position: relative;
  min-height: 100vh;
}

#content-wrap {
  padding-bottom: 4rem;    /* Footer height */
}

.banner {
  width: 100%;
  margin-bottom: -.4%;
}

.login-panel {
  color: white;
  position: absolute;
  right: 0;
  margin: 1pc;
  height: 5%;
  width: 20%;
}

.register-link {
  text-align: end;
}

.navbar {
  height: 5%;
  margin: -0%;
  background: yellow;
}

.navbar-item {
  width: 20%;
  height: 2pc;
  display: inline-block;
}

#popular-hunts  {
  width: 30%;
  margin-top: 0%;
  background: green;
  display: inline-block;
}

.popular-hunts-card {
  margin: 1pc;
  -webkit-align-items: center;
          align-items: center;
  overflow: auto;
  -webkit-flex-direction: column;
          flex-direction: column;
  background: bisque;
}

.popular-hunts-card-image {
  -webkit-flex: 0 1 auto;
          flex: 0 1 auto;
  float: right;
  position: absolute;
}

.popular-hunts-card-text {
  display: inline-block;
}

#news {
  width: 70%;
  margin-top: 0%;
  background: red;
  display: inline-block;
  vertical-align: top;
}

.news-card {
  margin: 1pc;
  -webkit-align-items: center;
          align-items: center;
  overflow: auto;
  -webkit-flex-direction: column;
          flex-direction: column;
  background: lightcoral;
}

.news-card-text {
  display: inline-block;
}

#register-login-page{
  background: #08081A;
}

#footer {
  background: darkblue;
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 4.5rem; 
}


